@import "../mixins";
.main{
  padding: 23.75em 0 0;
  background-color: #dcf5b8;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 82em calc(100% - 82em);

  }
  &__img{
    align-self: flex-end;
    img{
      margin-left: -10em;
      width: 131.8%;
      display: block;
    }
  }
  &__info{
    @include grid100gap(2em);
  }
  &__title{
    font-size: 4.375em;
    line-height: 114%;
    font-weight: 700;
  }
  &__subtitle{
    font-size: 3.5em;
    line-height: 142.8%;
  }
  .m-list{
    margin-top: 5.5em;
  }
  &__nav{
    display: flex;
    margin-top: 6.875em;
  }
}

.shifts{
  padding: 14em 0 15.25em;
  background: #f0ffda;
  &__title{
    max-width: 19em;
  }
  &__nav{
    display: flex;
    margin-top: 6.5em;
  }
  &__grid{
    margin-top: 5.625em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.8125em));
    gap: 5.625em;
  }
}

.program{
  padding: 12.5em 0;
  &__grid{
    margin-top: 8.75em;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 6.25em 2em;
  }
}

.prices{
  padding: 11.25em 0 15em;
  background: #f0ffda;
  &__top{
    @include grid100gap(6em);
    margin-bottom: 4em;
  }
  &__subtitle{
    text-transform: unset;
    span{
      font-weight: 400;
    }
  }
  .m-list{
    margin: 4em 0 4.5em;
  }
  &__grid{
    @include grid100gap(2.25em);
    margin-top: 4.625em;
  }
}

.what-do{
  padding: 13.75em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 8.125em));
    gap: 16.25em;
    margin-top: 5em;
  }
}

.about{
  padding: 11.875em 0 12.5em;
  background: #f0ffda;
  &__desc{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 11.25em;
    margin-top: 6em;
  }
  &__slider{
    margin-top: 8.5em;
    position: relative;
    .swiper{
      &-container{
        width: calc(100% + 3em);
        overflow: hidden;
      }
      &-slide{
        padding-right: 3em;
      }
      &-button{
        width: 4.4375em;
        height: auto;
        top: 50%;
        margin: 0;
        transform: translateY(-50%);
        img{
          width: 100%;
          display: block;
        }
        &-prev{
          right: 100%;
          left: unset;
          margin-right: 4em;
        }
        &-next{
          left: 100%;
          right: unset;
          margin-left: 4em;
        }
      }
    }
  }
  &__img{
    @include imgCover(75%);
  }
  &__grid{
    margin-top: 9.375em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12.5em;
  }
}

.info{
  &__text{
    margin-top: 2.25em;
    @include grid100gap(1.75em);
    p{
      line-height: inherit;
    }
  }
}

.nums{
  &__groups{
    margin-top: 5.25em;
    @include grid100gap(6.5em);
  }
  &__group{
    display: flex;
    &-img{
      padding: .25em 0;
      width: 3.75em;
      flex-shrink: 0;
      margin-right: 2em;
      img{
        width: 100%;
        display: block;
      }
    }
    &-content{
      @include grid100gap(1em);
    }
    .display2{
      text-transform: unset;
    }
  }
}

.how{
  padding: 12.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 22.5em;
    margin-top: 8.75em;
  }
}

.consult{
  padding: 11.25em 0 0;
  background: #f0ffda;
  overflow: hidden;
  border-bottom: .875em solid #73a52c;
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: 74.9375em auto;
    gap: 4.25em;
  }
  &__content{
    padding-top: 3em;
  }
  &__img{
    img{
      width: 116.1%;
      display: block;
    }
  }
}

.ceo{
  margin-top: 4.75em;
  display: flex;
  align-items: flex-end;
  &__info{
    @include grid100gap(1em);
    .display2{
      font-style: italic;
    }
  }
  &__contacts{
    display: flex;
    align-items: center;
    margin-top: 3.25em;
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-left: 2.25em;
    &-item{
      width: 6em;
      display: block;
      &:not(:last-child){
        margin-right: 1.375em;
      }
      &:hover{
        img{
          transform: translateY(-15%);
        }
      }
      img{
        width: 100%;
        transition: all .3s;
      }
    }
  }
}

.avatar{
  width: 18.25em;
  height: 18.25em;
  flex-shrink: 0;
  margin-right: 4em;
  img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 992px){
  .main{
    padding-top: 6.5em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__img{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      img{
        width: 100%;
        margin: 0;
      }
    }
    &__info{
      gap: 1em;
    }
    &__title{
      font-size: 2em;
    }
    &__subtitle{
      font-size: 1.5em;
    }
    .m-list{
      margin-top: 2em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__btn{
      width: 100%;
    }
  }

  .shifts{
    padding: 3em 0;
    &__title{
      span{
        white-space: nowrap;
      }
    }
    &__grid{
      margin-top: 2em;
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__btn{
      width: 100%;
    }
  }

  .program{
    padding: 3em 0;
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,1fr);
      gap: 2em;
    }
  }

  .prices{
    padding: 3em 0;
    &__top{
      gap: 1.5em;
      margin-bottom: 2em;
    }
    &__subtitle{
      font-size: 1.25em;
      span{
        white-space: nowrap;
      }
    }
    .m-list{
      margin: 2em 0;
    }
    &__grid{
      margin-top: 2em;
      gap: 1em;
    }
  }

  .what-do{
    padding: 3em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 1em;
    }
  }

  .about{
    padding: 3em 0;
    overflow: hidden;
    &__desc{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 1.5em;
    }
    &__slider{
      margin-top: 1.5em;
      .swiper{
        &-container{
          width: 100%;
          overflow: unset;
        }
        &-slide{
          padding-right: 1em;
          width: 85%;
        }
        &-button{
          display: none;
        }
      }
    }
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .info{
    &__text{
      margin-top: 1.5em;
      gap: 1em;
    }
  }

  .nums{
    &__groups{
      margin-top: 1.5em;
      gap: 2em;
    }
    &__group{
      &-img{
        width: 1.5em;
        margin-right: .5em;
      }
      &-content{
        gap: .5em;
      }
      .display2{
        font-size: 1.25em;
      }
    }
  }

  .how{
    padding: 3em 0;
    &__grid{
      margin-top: 2em;
      grid-template-columns: 100%;
      gap: 2em;
    }
  }

  .consult{
    padding-top: 3em;
    border-width: 3px;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 1em;
    }
    &__content{
      padding: 0;
    }
    &__img{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      img{
        width: 100%;
        margin: 0;
      }
    }
  }

  .ceo{
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 1.5em;
    &__contacts{
      flex-direction: column;
      margin-top: 0;
    }
    &__socials{
      margin: 1em 0 0;
      &-item{
        width: 2.5em;
        &:not(:last-child){
          margin-right: 1em;
        }
      }
    }
  }

  .avatar{
    width: 7.5em;
    height: 7.5em;
    margin: 0 0 1em;
  }
}


@media screen and (max-width: 576px){
  .program{
    &__title{
      font-size: 1.25em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
