@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    padding: 0 4em;
    height: 6.875em;
    background-color: $primary;
    box-shadow: 0 0 2.8125em .25em rgba(0,1,1,0.14);
    border-radius: 3.5em;
    text-transform: uppercase;
    color: #ffffff;
    span{
        font-size: 2.125em;
    }
    &:hover{
        background-color: darken($primary,10%);
        color: #ffffff;
    }
}


@media screen and (max-width: 992px){
    .m-btn-primary{
        padding: 0 1em;
        height: 4em;
        span{
            font-size: 1.125em;
        }
    }
}
