@import 'vars';
html {
    width: 100%;
    font-size: unquote("min(100vw, 2340px)");
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: .683759%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 2380px;
    width: 100%;
    margin: 0 auto;
    padding: 0 2.5em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .page{
        font-size: 2.2%;
    }

    .container{
        padding: 0 1.25em;
    }
}


@media screen and (max-width: 576px){
    .page{
        font-size: 4.27%;
    }
}
