@import "../mixins";
.shift-card{
  &._green{
    .shift-card__title{
      color: $green;
    }
  }
  &__title{
    text-transform: unset;
    max-width: 18em;
    color: $primary;
  }
  &__img{
    margin: 4.625em 0 6em;
    @include imgCover(41.7%);
  }
}

.program-card{
  display: flex;
  align-items: center;
  &__img{
    margin-right: 4.375em;
    flex-shrink: 0;
    width: 8em;
    height: 8em;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.what-card{
  display: flex;
  align-items: center;
  border-bottom: .9375em solid $primary;
  padding: 2.75em 0;
  &._blue{
    border-color: $blue;
  }
  &__num{
    font-weight: 700;
    flex-shrink: 0;
    margin-right: 3.75em;
    span{
      font-size: 6.25em;
    }
  }
}

.how-card{
  position: relative;
  &:not(:last-child){
    &:before{
      content: '';
      position: absolute;
      width: 22.5em;
      height: 11.25em;
      background-image: url(../img/arrow.webp);
      @include bgImgSize(4.4375em);
      left: 100%;
      bottom: 0;
    }
  }
  &._green{
    .how-card__num{
      color: #73a52c;
    }
  }
  &._blue{
    .how-card__num{
      color: $blue;
    }
  }
  &__num{
    font-weight: 700;
    color: #a464a4;
    margin-bottom: 3em;
    span{
      font-size: 12.5em;
    }
  }
  &__desc{
    a{
      font-weight: 700;
    }
  }
}


@media screen and (min-width: 993px){
  .price-card{
    display: grid;
    grid-template-columns: 21em 51em 34em auto;
    gap: .3125em;
    &__col{
      padding: 0 2.5em;
      height: 7.25em;
      display: flex;
      align-items: center;
      color: #fff;
      &._purple{
        background-color: #a464a4;
      }
      &._green{
        background-color: #73a52c;
      }
      &._blue{

      }
      &:first-child{
        border-radius: 1em 0 0 1em;
      }
      &:nth-child(3){
        padding-right: 0;
        position: relative;
        &:before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: calc(100% - 5.5em);
          height: 100%;
          background-color: #008dd2;
        }
        .price-card__date{
          position: relative;
          z-index: 2;
        }
      }
    }
    &__arrow{
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 4.4375em;
    }
    &__triangle{
      position: relative;
      height: 100%;
      width: 3.5em;
      overflow: hidden;
      margin-left: .25em;
      &:before{
        content: '';
        position: absolute;
        border-top: 4em solid transparent;
        border-left: 3.75em solid #008dd2;
        border-bottom: 4em solid transparent;
        margin-left: -.25em;
        width: 0;
        height: 0;
        top: 50%;
        transform: translate(0%,-50%);
        right: 0;
      }
    }
    &__title{
      text-transform: unset;
    }
    &__date{
      font-weight: 400;
      text-transform: unset;
    }
    &__btn{
      padding: 0;
      height: 100%;
      width: 100%;
      margin-left: 2em;
    }
  }
}


@media screen and (max-width: 992px){
  .shift-card{
    &__img{
      margin: 1em 0;
    }
    &__title{
      font-size: 1.25em;
      br{
        display: none;
      }
    }
  }

  .program-card{
    &__img{
      width: 4em;
      height: 4em;
      margin-right: 1em;
    }
  }

  .price-card{
    padding: 1.25em 1em;
    border-radius: 1em;
    border: 1px solid $primary;
    @include grid100gap(1em);
    &__arrow{
      display: none;
    }
    &__date{
      font-weight: 400;
      font-size: 1.25em;
    }
  }

  .what-card{
    padding: .5em 0;
    border-width: 3px;
    &__num{
      margin-right: .5em;
      span{
        font-size: 1.5em;
      }
    }
  }

  .how-card{
    &:before{
      display: none;
    }
    &__num{
      margin-bottom: 1em;
      span{
        font-size: 4em;
      }
    }
  }
}
