.display2{
  font-size: 3.5em;
  line-height: 125%;
  text-transform: uppercase;
  font-weight: 700;
}

.text36{
  font-size: 2.25em;
  line-height: 167%;
}

.m-list{
  @include grid100gap(1em);
  li{
    display: flex;
    &:before{
      content: '';
      display: block;
      flex-shrink: 0;
      width: 1.6875em;
      height: 1.6875em;
      border-radius: 50%;
      background: #73a52c;
      margin-right: 2em;
      transform: translateY(.75em);
    }
    span{
      font-size: 2.625em;
      line-height: 121.4%;
    }
  }
}

.blue-text{
  color: $blue;
}


@media screen and (max-width: 992px){
  .display2{
    font-size: 1.5em;
  }

  .text36{
    font-size: .875em;
  }

  .m-list{
    gap: .75em;
    li{
      font-size: .375em;
    }
  }
}
