.footer{
  padding: 6.25em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__address{
    b{
      font-weight: 700;
    }
  }
  &__conf{
    text-decoration: underline;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.25em 0;
    &__inner{
      display: block;
    }
    &__address{
      margin-bottom: 1em;
    }
  }
}
