@import "../mixins";
.header{
  position: absolute;
  top: 0;
  left: 0;
  padding: 2.875em 0;
  width: 100%;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    margin-right: 4.625em;
    display: block;
    width: 19.375em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{

  }
}

.menu{
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    text-decoration: underline;
    &:not(:last-child){
      margin-right: 5.25em;
    }
    span{
      font-size: 2.125em;
      line-height: 130%;
    }
  }
}

.phone-link{
  display: flex;
  align-items: center;
  font-weight: 700;
  color: #363636;
  span{
    font-size: 3.75em;
  }
  img{
    display: block;
    width: 3.25em;
    flex-shrink: 0;
    margin-right: 1.25em;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: .75em 0;
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      position: relative;
      z-index: 11;
      width: 7em;
      margin: 0;
    }
    &._open{
      .burger{
        background-size: 140% auto;
        background-image: url(../img/burger2.svg);
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .menu{
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    background: #dcf5b8;
    padding: 6em 1.25em 2em;
    display: block;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    .phone-link{
      margin-top: 3em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: unset;
    &__link{
      &:not(:last-child){
        margin: 0 0 1.5em;
      }
      span{
        font-size: 1.25em;
      }
    }
  }

  .phone-link{
    span{
      font-size: 1.5em;
    }
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }

  .burger{
    position: relative;
    z-index: 11;
    width: 2em;
    height: 2em;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
    display: block;
  }
}
