@import 'components/_fonts.scss';
@import 'reset';
@import '_global';
@import '_ui';
@import 'page-blocks/page-blocks.scss';
@import 'layout/layout.scss';
@import "pages/index";


@media screen and (min-width: 993px){
  @media screen and (max-width: 2600px){
    .wrapper{
      font-size: .875em;
    }
    .container{
      padding: 0 12em;
    }
  }
}


.reviews{
  padding: 14em 0;
  background: #f0ffda;
}

.reviews-slider{
  margin-top: 6em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 3.9em);
      overflow: hidden;
    }
    &-slide{
      width: 25%;
      padding-right: 4em;
    }
    &-button{
      width: 4.4375em;
      height: auto;
      top: 50%;
      margin: 0;
      transform: translateY(-50%);
      img{
        width: 100%;
        display: block;
      }
      &-prev{
        right: 100%;
        left: unset;
        margin-right: 4em;
      }
      &-next{
        left: 100%;
        right: unset;
        margin-left: 4em;
      }
    }
  }
}

.review-card{
  &__video{
    position: relative;
    padding-bottom: 175%;
    iframe{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .reviews{
    padding: 3em 0;
  }

  .reviews-slider{
    margin-top: 2em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 14em;
        padding-right: 1em;
      }
      &-button{
        display: none;
      }
    }
  }
}
